.review_b{
    width: 100%;

    /* border: 2px solid black; */
}
.review_b h1{
   text-align:center;
   font-size: 2.4rem;
   padding: 1rem 0;
   color: var(--secColor);
}
.review_div_green{
    width: 100%;
    height: 10rem;
    margin: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 3rem ;
    border-radius: 1rem;
    background-color: rgb(157,235,157);
    box-shadow:3px 3px 5px 0px rgba(0,0,0,0.5);
}
.review_div_red{
    width: 100%;
    height: 10rem;
    color: #fff;
    margin: 1rem;
    display: flex;
    border-radius: 1rem;
    background-color: rgb(255,133,133);
    justify-content: space-between;
    align-items: center;
    padding: 0 3rem ;
    box-shadow:3px 3px 5px 0px rgba(0,0,0,0.5);
}

.review_div_green div p,.review_div_red div p{
  text-align: center;
  margin-top: 1rem;
  font-size: 1.5rem;
}
.r1{
    width:92%;
    overflow-y: auto;
}
.r2{
    width: 8%;
    border-left: 2px solid;
    padding: 0 1rem ;
}
.r2 p{
   
}

@media (max-width:680px){
    .review_div_green div p,.review_div_red div p{
        text-align: center;
        margin-top: 1rem;
        font-size: 1.3rem;
      }
      .r1{
        width:90%;
    }
    .r2{
        width: 10%;
    }
}
@media (max-width:520px){
    .review_div_green div p,.review_div_red div p{
        text-align: center;
        margin-top: 1rem;
        font-size: 1rem;
      }
      .r1{
        width:88%;
    }
    .r2{
        width: 12%;
    }
}
